import React from "react";

interface IAnnotationCheckboxProps {
  readonly checked: boolean;
  readonly id: string;
  readonly onChange: () => void;
  readonly taxonomyId: string;
}
export const AnnotationCheckbox = ({
  checked,
  id,
  onChange,
  taxonomyId,
}: IAnnotationCheckboxProps) => (
  <input
    checked={checked}
    id={`annotation-checkbox-${taxonomyId}-${id}`}
    onChange={onChange}
    onClick={e => {
      e.stopPropagation();
    }}
    type="checkbox"
  />
);
AnnotationCheckbox.displayName = "AnnotationCheckbox";
