import { useMemo } from "react";

import { withHistory } from "@ui/util/withHistory";
import { IPredictions, ITaxonomies } from "../types";
import { State } from "./index";

export const useDefaultState = (predictions: IPredictions, taxonomies: ITaxonomies) =>
  useMemo((): State => {
    const annotations: State["annotations"] = {};
    for (const predictionKey of Object.keys(predictions)) {
      if (!(predictionKey in taxonomies)) {
        continue;
      }
      const uniquePredictions = {};
      predictions[predictionKey].forEach(p => {
        const termLowerCase = p.prediction.trim().toLowerCase();
        if (termLowerCase.length < 1 || termLowerCase in uniquePredictions) {
          return;
        }
        uniquePredictions[termLowerCase] = true;
        if (!(predictionKey in annotations)) {
          annotations[predictionKey] = [];
        }
        annotations[predictionKey].push({
          confidence: p.confidence || 0.0,
          humanAdded: false,
          term: p.prediction,
          termLowerCase,
        });
      });
    }
    for (const taxonomyKey of Object.keys(taxonomies)) {
      if (taxonomyKey in annotations) {
        continue;
      }
      annotations[taxonomyKey] = [];
    }
    return {
      annotationHistory: withHistory(annotations),
      annotations,
    };
  }, []);
