import React from "react";

import { MetadataNode } from "./MetadataNode";

const isScalar = (input: any): boolean =>
  typeof input === "string" || typeof input === "number" || typeof input === "boolean";

interface IProps {
  readonly item: any;
}

export const NodeContents = ({ item }: IProps) => {
  if (Array.isArray(item)) {
    return (
      <ul className="mb-0">
        {item.map((i, index) => (
          <li key={`item-${index}`}>
            <NodeContents item={i} />
          </li>
        ))}
      </ul>
    );
  }
  if (typeof item === "object") {
    return <MetadataNode metadata={item} />;
  }
  if (isScalar(item)) {
    return <>{item}</>;
  }
  return null;
};
NodeContents.displayName = "NodeContents";
