import React from "react";
import { ButtonGroup } from "reactstrap";

interface IProps {
  readonly html: string | undefined;
  readonly pdf: string | undefined;
  readonly xml: string | undefined;
}

export const DocumentSources = (props: IProps) => {
  const nonEmptyKeys: Array<string> = Object.keys(props).reduce(
    (prev: Array<string>, next: string): Array<string> =>
      props[next] ? prev.concat([next]) : prev,
    [],
  );
  if (nonEmptyKeys.length < 1) {
    return null;
  }
  return (
    <ButtonGroup className="document-sources">
      {nonEmptyKeys.map((source: string, index: number) => (
        <a className="btn btn-outline-primary" key={source} href={props[source]} target="_blank">
          View {source.toLocaleUpperCase()}
        </a>
      ))}
    </ButtonGroup>
  );
};
DocumentSources.displayName = "DocumentSources";
