import React from "react";

import { TAG_BLOCK } from "../constants";
import { IFontInformation } from "../types";
import { extractTextBlock } from "./util";

type Props = Readonly<{
  el: Element;
  fontInformation?: IFontInformation;
}>;
export const Text = ({ el, fontInformation }: Props) => {
  const id: string = el.getAttribute("id") || "";
  const blocks = Array.from(el.getElementsByTagName(TAG_BLOCK));
  return (
    <p>{blocks.map((b, index) => extractTextBlock(b, `${id}-block-${index}-`, fontInformation))}</p>
  );
};
Text.displayName = "Text";
