import React from "react";
import { IFontInformation } from "../types";
import { Row } from "./Row";
import { extractTableInformation } from "./util";

type Props = Readonly<{
  el: Element;
  fontInformation?: IFontInformation;
}>;

export const Table = ({ el, fontInformation }: Props) => {
  const { headers, rows } = extractTableInformation(el, fontInformation);
  return (
    <table className="table table-bordered table-sm table-striped">
      <thead>
        {headers.map((r, rowIndex) => (
          <Row key={`row-${rowIndex}`} isHeader row={r} />
        ))}
      </thead>
      <tbody>
        {rows.map((r, rowIndex) => (
          <Row key={`row-${rowIndex}`} isHeader={false} row={r} />
        ))}
      </tbody>
    </table>
  );
};
Table.displayName = "Table";
