import React, { MouseEvent, ReactElement, RefObject, useMemo, useRef, useState } from "react";

interface IChildArgs {
  readonly dragging: boolean;
  readonly height: number;
  readonly onDrag: (e: MouseEvent) => void;
  readonly ref: RefObject<HTMLDivElement>;
  readonly resizeBar: ReactElement;
  readonly startDrag: (e: MouseEvent) => void;
  readonly stopDrag: (e: MouseEvent) => void;
}

interface IProps {
  readonly children: (args: IChildArgs) => ReactElement;
  readonly onResize: (size: number) => void;
  readonly initialHeight: number;
}

interface IState {
  readonly dragState: {
    readonly initialHeight: number;
    readonly startPos: number;
  } | null;
  readonly height: number;
}

export const ResizeHandler = (props: IProps) => {
  const abstractRef = useRef<HTMLDivElement>(null);
  const resizeBarRef = useRef<HTMLDivElement>(null);
  const initialState = useMemo(() => ({ dragState: null, height: props.initialHeight }), [
    props.initialHeight,
  ]);
  const [state, setState] = useState<IState>(initialState);
  const { dragState, height } = state;
  const startDrag = (e: MouseEvent) => {
    if (e.button !== 0 || !abstractRef.current || !resizeBarRef.current) {
      return;
    }
    const handleBars = resizeBarRef.current.getBoundingClientRect() as DOMRect;
    if (e.clientY < handleBars.y || e.clientY > handleBars.y + handleBars.height) {
      return;
    }
    setState({
      ...state,
      dragState: {
        initialHeight: abstractRef.current.clientHeight,
        startPos: e.pageY,
      },
    });
  };
  const stopDrag = (_: MouseEvent) => {
    setState({ ...state, dragState: null });
  };
  const onDrag = (e: MouseEvent) => {
    if (!dragState) {
      return;
    }
    const { pageY } = e;
    const { initialHeight, startPos } = dragState;
    const selection = window.getSelection();
    if (selection !== null) {
      selection.removeAllRanges();
    }
    // calculate the new height of the component
    const calculatedHeight = Math.min(500, Math.max(50, initialHeight + (pageY - startPos)));
    props.onResize(calculatedHeight);
    setState({ ...state, height: calculatedHeight });
  };
  return props.children({
    dragging: dragState !== null,
    height,
    onDrag,
    ref: abstractRef,
    resizeBar: (
      <div className="bg-secondary resize-bar" ref={resizeBarRef}>
        <span className="iconify" data-icon="simple-line-icons:menu" data-inline="false" />
      </div>
    ),
    startDrag,
    stopDrag,
  });
};
