import React, { ReactNode } from "react";

import {
  ATTR_RGB,
  TAG_BOLD,
  TAG_BREAK,
  TAG_COLOR,
  TAG_FONT,
  TAG_ITALICS,
  TAG_NON_EXTRACT,
  TAG_SUBSCRIPT,
  TAG_SUPERSCRIPT,
  TAG_TCY,
  TAG_UNDERLINE,
} from "../constants";
import { FontClass, IFontInformation } from "../types";
import { extractSizeFromFontElement } from "../util";

export const mapFontSizeToClass = (size: number, fontInformation: IFontInformation): FontClass => {
  if (isNaN(size) || size === fontInformation.mode) {
    return FontClass.Normal;
  }
  if (size > fontInformation.mode) {
    return size > fontInformation.upperBound ? FontClass.Larger : FontClass.Large;
  }
  return size < fontInformation.lowerBound ? FontClass.Smaller : FontClass.Small;
};

/**
 * Extract text from the <inno:text> tag. Possible sub tags include:
 * <br> line break
 * <i> italics
 * <b> bold
 * <u> underlined
 * <sup> superscript
 * <sub> subscript
 * <tcy> weird chinese thing
 * <font> font information
 * <color> text foreground
 * <nec> non-extractable
 * @param el Element The <ns0:text> element.
 * @param keyPrepend A key to prend for unique React keys.
 * @return Array<ReactNode> Returns a renderable list of React nodes.
 */
export const extractTextBlock = (
  el: Element,
  keyPrepend: string,
  fontInformation: IFontInformation | undefined,
): Array<ReactNode> => {
  const { childNodes } = el;
  let contents: Array<ReactNode> = [];
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < childNodes.length; i++) {
    const child = childNodes[i];
    if (child instanceof Element) {
      const key = `${keyPrepend}${child.nodeName}-${contents.length}`;
      const innerContents = extractTextBlock(child, `${key}-`, fontInformation);
      switch (child.nodeName) {
        // map to a line break
        case TAG_BREAK: {
          // for our use case, we do not need to preserve the line breaks due to natural wrapping
          // in the original document...rather we can allow the paragraph text to flow naturally
          // instead for better readability
          contents.push(" ");
          break;
        }
        // map to strong bold text
        case TAG_BOLD: {
          contents.push(
            <span className="font-weight-bold" key={key}>
              {innerContents}
            </span>,
          );
          break;
        }
        // map to italics
        case TAG_ITALICS: {
          contents.push(
            <span className="font-italic" key={key}>
              {innerContents}
            </span>,
          );
          break;
        }
        // map to underline
        case TAG_UNDERLINE: {
          contents.push(<u key={key}>{innerContents}</u>);
          break;
        }
        // map to subscript
        case TAG_SUBSCRIPT: {
          contents.push(<sub key={key}>{innerContents}</sub>);
          break;
        }
        // map to superscript
        case TAG_SUPERSCRIPT: {
          contents.push(<sup key={key}>{innerContents}</sup>);
          break;
        }
        // map to color
        case TAG_COLOR: {
          const rgb = (child.getAttribute(ATTR_RGB) || "").trim().toLowerCase();
          if (rgb.length < 1) {
            contents = contents.concat(innerContents);
            break;
          }
          contents.push(
            <span key={key} style={{ color: rgb }}>
              {innerContents}
            </span>,
          );
          break;
        }
        case TAG_NON_EXTRACT: {
          contents.push(<span key={key}>&#x25A1;</span>);
          break;
        }
        case TAG_FONT: {
          if (fontInformation !== undefined) {
            contents.push(
              <span
                key={key}
                className={mapFontSizeToClass(extractSizeFromFontElement(child), fontInformation)}
              >
                {innerContents}
              </span>,
            );
            break;
          }
          contents = contents.concat(innerContents);
          break;
        }
        // for the following tags, we generally ignore it and just push the inner contents
        case TAG_TCY: {
          contents = contents.concat(innerContents);
          break;
        }
        default: {
          contents = contents.concat(innerContents);
          console.warn("Unhandled tag in text:", child.nodeName);
          break;
        }
      }
      continue;
    }
    contents.push(child.textContent);
  }
  return contents;
};
