import React from "react";
import { Col, Row } from "reactstrap";

import { IIndexingDocument } from "../../types";
import { NodeContents } from "./NodeContents";

interface IProps {
  readonly metadata: IIndexingDocument["metadata"];
}

export const MetadataNode = ({ metadata }: IProps) => {
  if (!metadata) {
    return null;
  }
  const keys: Array<string> = Object.keys(metadata);
  if (keys.length < 1) {
    return null;
  }
  return (
    <>
      {keys.map(
        (key: string): React.ReactElement => (
          <Row className="metadata-row" key={key}>
            {" "}
            <Col className="col-auto metadata-label">{key}</Col>{" "}
            <Col className="col-auto metadata-value">
              <NodeContents item={metadata[key]} />
            </Col>{" "}
          </Row>
        ),
      )}
    </>
  );
};
MetadataNode.displayName = "MetadataNode";
