import cx from "classnames";
import React, { useEffect, useState } from "react";
import { ButtonGroup } from "reactstrap";

import { IActionMap } from "../../../state/actions";
import { IAnnotation } from "../../../types";

interface IProps {
  readonly actions: IActionMap;
  readonly annotations: ReadonlyArray<IAnnotation>;
  readonly checkedTerms: ReadonlyArray<string>;
  readonly taxonomyId: string;
}

type LAST_SORT = "ALPHA DESC" | "ALPHA ASC" | "CONF DESC" | "CONF ASC" | null;

export const ActionsRow = ({ actions, annotations, checkedTerms, taxonomyId }: IProps) => {
  const [lastSort, setLastSort] = useState<LAST_SORT>(null);
  useEffect(() => {
    setLastSort(null);
  }, [annotations]);
  return (
    <div className="annotations-list-row actions">
      <ButtonGroup aria-label="Reorder Terms" className="actions" role="group">
        <button
          className="btn btn-outline-secondary"
          disabled={checkedTerms.length < 1}
          onClick={() => {
            actions.reorderAnnotations(checkedTerms, "UP", taxonomyId);
          }}
          type="button"
        >
          <i className="fa fa-long-arrow-up" aria-label="Move Up" />
        </button>
        <button
          className="btn btn-outline-secondary"
          disabled={checkedTerms.length < 1}
          onClick={() => {
            actions.reorderAnnotations(checkedTerms, "DOWN", taxonomyId);
          }}
          type="button"
        >
          <i className="fa fa-long-arrow-down" aria-label="Move Down" />
        </button>
      </ButtonGroup>
      <ButtonGroup aria-label="Sort Terms" className="actions" role="group">
        <button
          className="btn btn-outline-primary"
          onClick={() => {
            const direction = lastSort === "ALPHA DESC" ? "ASC" : "DESC";
            setLastSort(direction === "DESC" ? "ALPHA DESC" : "ALPHA ASC");
            actions.sortAnnotations("ALPHA", direction, taxonomyId);
          }}
          type="button"
        >
          <i
            className={cx({
              fa: true,
              "fa-sort-alpha-asc": lastSort !== "ALPHA DESC",
              "fa-sort-alpha-desc": lastSort === "ALPHA DESC",
            })}
            aria-label="Sort Alphabetically"
          />
        </button>
        <button
          className="btn btn-outline-primary"
          onClick={() => {
            const direction = lastSort === "CONF DESC" ? "ASC" : "DESC";
            setLastSort(direction === "DESC" ? "CONF DESC" : "CONF ASC");
            actions.sortAnnotations("CONF", direction, taxonomyId);
          }}
          type="button"
        >
          <i
            className={cx({
              fa: true,
              "fa-sort-numeric-asc": lastSort === "CONF DESC",
              "fa-sort-numeric-desc": lastSort !== "CONF DESC",
            })}
            aria-label="Sort Alphabetically"
          />
        </button>
      </ButtonGroup>
      <ButtonGroup aria-label="Delete Terms" className="actions" role="group">
        <button
          className="btn btn-danger"
          disabled={checkedTerms.length < 1}
          onClick={() => {
            actions.deleteAnnotations(checkedTerms, taxonomyId);
          }}
          type="button"
        >
          <i className="fa fa-trash" aria-label="Delete" />
        </button>
      </ButtonGroup>
    </div>
  );
};
ActionsRow.displayName = "ActionsRow";
