import React from "react";

interface IProps {
  count: number;
  title: string;
}

export const Header = ({ count, title }: IProps) => {
  let terms = null;
  if (count > 0) {
    terms = (
      <span className="term-count">
        {count} term{count > 1 ? "s" : ""}
      </span>
    );
  }
  return (
    <div className="annotations-header">
      <h4 className="text-truncate title">{title}</h4>
      {terms}
    </div>
  );
};
Header.displayName = "Header";
