import { State } from "./index";
import { PostMessage } from "./messages";

export enum ActionType {
  ADD_ANNOTATION,
  DELETE_ANNOTATIONS,
  REDO,
  REORDER_ANNOTATIONS,
  SORT_ANNOTATIONS,
  LOAD_PERSISTED_STATE,
  UNDO,
}

interface IBaseAction {
  postMessage: PostMessage;
  type: ActionType;
}

interface IAddAnnotationAction extends IBaseAction {
  taxonomyId: string;
  term: string;
  type: ActionType.ADD_ANNOTATION;
}

interface IDeleteAnnotationsAction extends IBaseAction {
  annotations: ReadonlyArray<string>;
  taxonomyId: string;
  type: ActionType.DELETE_ANNOTATIONS;
}

interface ILoadPersistedStateAction extends IBaseAction {
  persistedState: State["annotations"];
  type: ActionType.LOAD_PERSISTED_STATE;
}

interface IRedoAction extends IBaseAction {
  type: ActionType.REDO;
}

interface IReorderAnnotationsAction extends IBaseAction {
  annotations: ReadonlyArray<string>;
  direction: "UP" | "DOWN";
  taxonomyId: string;
  type: ActionType.REORDER_ANNOTATIONS;
}

interface ISortAnnotationsAction extends IBaseAction {
  by: "ALPHA" | "CONF";
  direction: "ASC" | "DESC";
  taxonomyId: string;
  type: ActionType.SORT_ANNOTATIONS;
}

interface IUndoAction extends IBaseAction {
  type: ActionType.UNDO;
}

export type Action =
  | IAddAnnotationAction
  | IDeleteAnnotationsAction
  | ILoadPersistedStateAction
  | IRedoAction
  | IReorderAnnotationsAction
  | ISortAnnotationsAction
  | IUndoAction;

type Dispatch = (action: Action) => void;

export interface IActionMap {
  addAnnotation: (term: string, taxonomyId: string) => void;
  deleteAnnotations: (annotations: ReadonlyArray<string>, taxonomyId: string) => void;
  reorderAnnotations: (
    annotations: ReadonlyArray<string>,
    direction: IReorderAnnotationsAction["direction"],
    taxonomyId: string,
  ) => void;
  redo: () => void;
  sortAnnotations: (
    by: ISortAnnotationsAction["by"],
    direction: ISortAnnotationsAction["direction"],
    taxonomyId: string,
  ) => void;
  undo: () => void;
}
export const actionMap = (dispatch: Dispatch, postMessage: PostMessage): IActionMap => ({
  addAnnotation: (term: string, taxonomyId: string) => {
    dispatch({
      postMessage,
      taxonomyId,
      term,
      type: ActionType.ADD_ANNOTATION,
    });
  },
  deleteAnnotations: (annotations: ReadonlyArray<string>, taxonomyId: string) => {
    dispatch({
      annotations,
      postMessage,
      taxonomyId,
      type: ActionType.DELETE_ANNOTATIONS,
    });
  },
  redo: () => {
    dispatch({
      postMessage,
      type: ActionType.REDO,
    });
  },
  reorderAnnotations: (
    annotations: ReadonlyArray<string>,
    direction: IReorderAnnotationsAction["direction"],
    taxonomyId: string,
  ) => {
    dispatch({
      annotations,
      direction,
      postMessage,
      taxonomyId,
      type: ActionType.REORDER_ANNOTATIONS,
    });
  },
  sortAnnotations: (
    by: ISortAnnotationsAction["by"],
    direction: ISortAnnotationsAction["direction"],
    taxonomyId: string,
  ) => {
    dispatch({
      by,
      direction,
      postMessage,
      taxonomyId,
      type: ActionType.SORT_ANNOTATIONS,
    });
  },
  undo: () => {
    dispatch({ postMessage, type: ActionType.UNDO });
  },
});
