import React from "react";

import { IAnnotation, ITaxonomyTerm } from "../../../../types";
import { Metadata } from "./Metadata";

interface IInnerContentsProps {
  readonly annotation: IAnnotation;
  readonly taxonomyId: string;
  readonly taxonomyTerm: ITaxonomyTerm | null;
}
export const InnerContents = ({ annotation, taxonomyId, taxonomyTerm }: IInnerContentsProps) => {
  const { term } = annotation;
  return (
    <div className="middle">
      {term}
      {taxonomyTerm && (
        <Metadata annotation={annotation} taxonomyId={taxonomyId} taxonomyTerm={taxonomyTerm} />
      )}
    </div>
  );
};
InnerContents.displayName = "InnerContents";
