import { IWithHistory } from "@ui/util/withHistory";

import { Action, ActionType } from "./actions";
import { annotations, State as AnnotationState } from "./annotations";
import { UpdateMessageType } from "./messages";

interface IStateTree {
  annotations: AnnotationState;
  annotationHistory: IWithHistory<AnnotationState>;
}
export type State = IStateTree;

export const reducer = (state: State, action: Action): State => {
  switch (action.type) {
    case ActionType.REDO: {
      action.postMessage({
        taxonomyId: "",
        type: UpdateMessageType.REDO,
      });
      return {
        ...state,
        annotations: state.annotationHistory.redo(),
      };
    }
    case ActionType.UNDO: {
      action.postMessage({
        taxonomyId: "",
        type: UpdateMessageType.UNDO,
      });
      return {
        ...state,
        annotations: state.annotationHistory.undo(),
      };
    }
    case ActionType.LOAD_PERSISTED_STATE:
      return {
        ...state,
        annotations: action.persistedState,
      };
    default: {
      const newAnnotations = annotations(state.annotations, action);
      if (newAnnotations === state.annotations) {
        return state;
      }
      state.annotationHistory.push(newAnnotations);
      return {
        ...state,
        annotations: newAnnotations,
      };
      break;
    }
  }
};
