import React, { Fragment, PureComponent } from "react";
import { CSSTransition } from "react-transition-group";

import { IAnnotation } from "../../../../types";

interface IProps {
  readonly annotation: IAnnotation;
  readonly expanded: boolean;
  readonly taxonomyId: string;
}

interface IState {
  readonly helpText: string | null;
  readonly loading: boolean;
}

export class ExpandedDetails extends PureComponent<IProps, IState> {
  constructor(props: IProps) {
    super(props);
    this.state = {
      helpText: null,
      loading: true,
    };
  }

  public componentDidUpdate(prev: IProps) {
    const { expanded } = this.props;
    if (expanded && !prev.expanded) {
      // @TODO extract help text from the taxonomy terms
      const help = "";
      setTimeout(() => {
        this.setState({
          helpText: help,
          loading: false,
        });
      }, 1000);
    }
  }

  public render() {
    // @TODO extract help text from the taxonomy terms
    const help = "";
    if (!help) {
      return null;
    }
    const { helpText, loading } = this.state;
    let contents = <i className="fa fa-spin fa-spinner" />;
    if (!loading && helpText) {
      const paragraphs = helpText
        .split("\n")
        .map(s => s.trim())
        .filter(s => s.length > 0);
      contents = (
        <Fragment>
          {paragraphs.map((text, index) => (
            // eslint-disable-next-line react/no-array-index-key
            <p key={`help-paragraph-${index}`}>{text}</p>
          ))}
        </Fragment>
      );
    }
    return (
      <CSSTransition classNames="show" in={this.props.expanded} timeout={300}>
        <div className="expanded-details">
          <div className="contents">{contents}</div>
        </div>
      </CSSTransition>
    );
  }
}
