import cx from "classnames";
import React, { useMemo } from "react";

import { IIndexingDocument } from "../types";

interface IProps {
  readonly title: IIndexingDocument["title"];
}

// document titles longer than this value will appear in smaller font
const CONDENSED_TITLE_BREAKPOINT = 250;

export const Title = ({ title }: IProps) => {
  let titleString = (title || "").trim();
  if (titleString.length < 1) {
    titleString = "(No title)";
  }
  const condensed = useMemo(() => titleString.length > CONDENSED_TITLE_BREAKPOINT, [
    titleString.length,
  ]);
  return <h1 className={cx("title", { condensed })}>{titleString}</h1>;
};
Title.displayName = "Title";
