import React from "react";
import { Toast, ToastBody, ToastHeader } from "reactstrap";

import { UpdateMessage, UpdateMessageType } from "../state/messages";
import { ClearMessage } from "../state/StateManager";
import { ITaxonomies } from "../types";
// import { useClearMessage } from "./useClearMessage";

interface IProps {
  clearMessage: ClearMessage;
  onRedo: () => void;
  onUndo: () => void;
  message: UpdateMessage | null;
  taxonomies: ITaxonomies;
}

// tslint:disable-next-line: cyclomatic-complexity
export const Message = ({ clearMessage, message, onRedo, onUndo, taxonomies }: IProps) => {
  // useClearMessage(clearMessage, message);
  let close = null;
  let messageBody = null;
  let messageTitle = null;
  const taxonomy =
    message !== null && message.taxonomyId in taxonomies ? taxonomies[message.taxonomyId] : null;
  if (message !== null) {
    close = (
      <button className="close" onClick={clearMessage} type="button">
        &times;
      </button>
    );
    const undo = (
      <div>
        <a
          href="#"
          onClick={e => {
            e.preventDefault();
            clearMessage();
            onUndo();
          }}
        >
          Undo
        </a>
      </div>
    );
    if (message.type === UpdateMessageType.UNDO) {
      const redo = (
        <div>
          <a
            href="#"
            onClick={e => {
              e.preventDefault();
              clearMessage();
              onRedo();
            }}
          >
            Redo
          </a>
        </div>
      );
      messageTitle = "Reverted change";
      messageBody = (
        <>
          <div>Reverted previous change (undo).</div>
          {redo}
        </>
      );
    } else if (message.type === UpdateMessageType.REDO) {
      messageTitle = "Reverted change";
      messageBody = (
        <>
          <div>Reverted previous state (redo).</div>
          {undo}
        </>
      );
    } else if (taxonomy !== null) {
      switch (message.type) {
        case UpdateMessageType.ADD_ANNOTATIONS:
          if (message.terms.length < 1) {
            break;
          }
          messageTitle = `Added term${message.terms.length > 1 ? "s" : ""}`;
          if (message.terms.length > 1) {
            messageBody = (
              <>
                <div>
                  Added <strong>{message.terms.length} new terms</strong> to {taxonomy.name}.
                </div>
                {undo}
              </>
            );
          } else {
            messageBody = (
              <>
                <div>
                  Added <strong>{message.terms[0]}</strong> to {taxonomy.name}.
                </div>
                {undo}
              </>
            );
          }
          break;
        case UpdateMessageType.DELETE_ANNOTATIONS:
          if (message.terms.length < 1) {
            break;
          }
          messageTitle = `Removed term${message.terms.length > 1 ? "s" : ""}`;
          if (message.terms.length > 1) {
            messageBody = (
              <>
                <div>
                  Removed <strong>{message.terms.length} terms</strong> from {taxonomy.name}.
                </div>
                {undo}
              </>
            );
          } else {
            messageBody = (
              <>
                <div>
                  Removed <strong>{message.terms[0]}</strong> from {taxonomy.name}.
                </div>
                {undo}
              </>
            );
          }
          break;
        case UpdateMessageType.REORDED_ANNOTATIONS:
          if (message.terms.length < 1) {
            break;
          }
          messageTitle = `Moved term${message.terms.length > 1 ? "s" : ""}`;
          if (message.terms.length > 1) {
            messageBody = (
              <>
                <div>
                  Moved <strong>{message.terms.length} terms</strong>{" "}
                  {message.direction === "UP" ? "up" : "down"} in {taxonomy.name}.
                </div>
                {undo}
              </>
            );
          } else {
            messageBody = (
              <>
                <div>
                  Moved <strong>{message.terms[0]}</strong>{" "}
                  {message.direction === "UP" ? "up" : "down"} in {taxonomy.name}.
                </div>
                {undo}
              </>
            );
          }
          break;
        case UpdateMessageType.SORTED_ANNOTATIONS:
          messageTitle = "Sorted terms";
          messageBody = (
            <>
              <div>
                Sorted terms in <strong>{taxonomy.name}</strong>{" "}
                {message.by === "ALPHA" ? "alphabetically" : "by confidence"}
                {message.direction === "DESC" ? "" : " (in reverse order)"}.
              </div>
              {undo}
            </>
          );
          break;

        default:
          throw new Error("Unhandled message type.");
      }
    }
  }
  const isOpen = message !== null && messageTitle !== null && messageBody !== null;
  return (
    <div className="message-wrapper">
      <Toast isOpen={isOpen}>
        <ToastHeader>
          <div className="toast-title">{messageTitle}</div>
          {close}
        </ToastHeader>
        <ToastBody>{messageBody}</ToastBody>
      </Toast>
    </div>
  );
};
Message.displayName = "Message";
