import { ReactNode } from "react";

import { ATTR_COL, ATTR_COLSPAN, ATTR_ROW, ATTR_ROWSPAN, TAG_COL, TAG_ROW } from "../constants";
import { extractTextBlock } from "../Text/util";
import { IFontInformation } from "../types";

export type Cell = Readonly<{
  col: number;
  colSpan: number;
  contents: Array<ReactNode>;
  rowSpan: number;
}>;

export type Row = Readonly<{
  cells: Array<Cell>;
  row: number;
}>;

type TableInformation = Readonly<{
  headers: Array<Row>;
  rows: Array<Row>;
}>;

export const extractTableInformation = (
  el: Element,
  fontInformation: IFontInformation | undefined,
): TableInformation => {
  const tableKey: string = el.getAttribute("id") || "";
  const headers: TableInformation["headers"] = [];
  const rowElements = Array.from(el.getElementsByTagName(TAG_ROW));
  const rows = rowElements.map(
    (rowEl, rowIndex): Row => {
      let row = parseInt(rowEl.getAttribute(ATTR_ROW) || "", 10);
      if (isNaN(row)) {
        row = rowIndex;
      }
      const cellElements = Array.from(rowEl.getElementsByTagName(TAG_COL));
      return {
        cells: cellElements.map(
          (cellEl, cellIndex): Cell => {
            let col = parseInt(cellEl.getAttribute(ATTR_COL) || "", 10);
            if (isNaN(col)) {
              col = cellIndex;
            }
            let colSpan = parseInt(cellEl.getAttribute(ATTR_COLSPAN) || "", 10);
            if (isNaN(colSpan)) {
              colSpan = 1;
            }
            let rowSpan = parseInt(cellEl.getAttribute(ATTR_ROWSPAN) || "", 10);
            if (isNaN(rowSpan)) {
              rowSpan = 1;
            }
            return {
              col,
              colSpan,
              contents: extractTextBlock(
                cellEl,
                `${tableKey}-row-${rowIndex}-cell-${cellIndex}-`,
                fontInformation,
              ),
              rowSpan,
            };
          },
        ),
        row,
      };
    },
  );
  return {
    headers,
    rows,
  };
};
