import cx from "classnames";
import React, { useMemo } from "react";

import { Annotations } from "./Annotations";
import { DocumentContents } from "./DocumentContents";
import { Message } from "./Message";
import { ResizeHandler } from "./ResizeHandler";
import { State } from "./state";
import { StateManager } from "./state/StateManager";
import { IIndexingDocument, IPredictions, ITaxonomies } from "./types";

import "./style.scss";

const DEFAULT_USER_HEIGHT = 300;
const USER_HEIGHT_PREFERENCE_KEY = "indexing-user-height-preference";

export interface IProps {
  readonly document: IIndexingDocument;
  readonly jobId: string;
  readonly onSave: (annotations: State["annotations"]) => Promise<void>;
  readonly predictions: IPredictions;
  readonly taxonomies: ITaxonomies;
  readonly saving: boolean;
}

// the maximum length of a single term
const MAX_TERM_LENGTH = 1024;

export const Indexing = ({ document, jobId, onSave, predictions, saving, taxonomies }: IProps) => {
  const initialHeight = useMemo(() => {
    if (window.localStorage) {
      const height = parseInt(window.localStorage.getItem(USER_HEIGHT_PREFERENCE_KEY) || "", 10);
      if (!isNaN(height)) {
        return height;
      }
    }
    return DEFAULT_USER_HEIGHT;
  }, [window.localStorage]);
  return (
    <div className="indexing">
      <ResizeHandler
        onResize={(resizeHandleHeight: number) => {
          if (window.localStorage) {
            window.localStorage.setItem(
              USER_HEIGHT_PREFERENCE_KEY,
              resizeHandleHeight.toString(10),
            );
          }
        }}
        initialHeight={initialHeight}
      >
        {({ dragging, height, onDrag, ref, resizeBar, startDrag, stopDrag }) => (
          <div
            className={cx("indexing-inner", { dragging })}
            onMouseDown={startDrag}
            onMouseLeave={stopDrag}
            onMouseUp={stopDrag}
            onMouseMove={onDrag}
          >
            <StateManager initialPredictions={predictions} jobId={jobId} taxonomies={taxonomies}>
              {({ actions, clearMessage, message, state: { annotations } }) => (
                <>
                  <div ref={ref} style={{ height }}>
                    <DocumentContents document={document} />
                  </div>
                  {resizeBar}
                  <div style={{ height: `calc(100vh - 15px - 56px - ${height}px)` }}>
                    <Annotations
                      actions={actions}
                      annotations={annotations}
                      loading={false}
                      maximumTermLength={MAX_TERM_LENGTH}
                      onSave={() => {
                        onSave(annotations);
                      }}
                      saving={saving}
                      taxonomies={taxonomies}
                    />
                  </div>
                  <Message
                    clearMessage={clearMessage}
                    message={message}
                    onRedo={actions.redo}
                    onUndo={actions.undo}
                    taxonomies={taxonomies}
                  />
                </>
              )}
            </StateManager>
          </div>
        )}
      </ResizeHandler>
    </div>
  );
};
Indexing.displayName = "Indexing";
