import React, { useEffect, useState } from "react";
import { Alert } from "reactstrap";

import { Loading } from "@ui/components/util/Loading";
import { IRenderJobOptions } from "@ui/helpers";
import { OUTPUT_ROLE } from "../../constants";
import { Indexing } from "../Indexing";
import { State } from "../Indexing/state";
import { IIndexingDocument, IInputTaxonomy, IPredictions, ITaxonomies } from "../Indexing/types";
import { buildDocumentAndPredictions, insertDataPoints } from "./innodom";

interface IProps {
  readonly opts: IRenderJobOptions<string>;
}

interface IState {
  document: IIndexingDocument | null;
  error: Error | null;
  predictions: IPredictions | null;
  saving: boolean;
  xmlDocument: Document | null;
}

const DEFAULT_STATE: IState = {
  document: null,
  error: null,
  predictions: null,
  saving: false,
  xmlDocument: null,
};

export const Handler = ({ opts }: IProps) => {
  const [state, setState] = useState<IState>(DEFAULT_STATE);
  const { document, error, predictions, saving, xmlDocument } = state;
  const [taxonomies, setTaxonomies] = useState<ITaxonomies | null>(null);
  useEffect(() => {
    if (!opts.state.job || !opts.state.contents || !opts.state.user || error !== null) {
      return;
    }
    if (document === null) {
      const { api_keys } = opts.state.user;
      const imageUris = opts.state.job.contents
        .filter(c => c.role === "zoning-pagehr-png")
        .map(c => `${c.uri}?token=${api_keys.live}`);
      setState({
        ...state,
        ...buildDocumentAndPredictions(
          opts.state.job,
          api_keys.live!,
          opts.state.contents,
          imageUris,
        ),
      });
    }
    if (taxonomies === null) {
      const taxonomyContents = opts.state.job.contents
        .filter(c => c.role === "indexing-taxonomy-json")
        .map(c => c.id);
      Promise.all(taxonomyContents.map(id => opts.actions.fetchContent(id)))
        .then((fetchedTaxonomies: Array<IInputTaxonomy>) => {
          const toLoad: ITaxonomies = {};
          for (const tax of fetchedTaxonomies) {
            toLoad[tax.id] = {
              id: tax.id,
              name: tax.name,
              open: true,
            };
          }
          setTaxonomies(toLoad);
        })
        .catch(err => {
          setState({ ...DEFAULT_STATE, error: err });
        });
    }
  });

  if (error !== null) {
    console.error(error);
    return <Alert color="danger">{error.message}</Alert>;
  }
  const {
    state: { job },
  } = opts;
  if (
    job === null ||
    document === null ||
    predictions === null ||
    taxonomies === null ||
    xmlDocument === null
  ) {
    return <Loading text="Loading..." />;
  }
  return (
    <Indexing
      document={document}
      jobId={job.id}
      onSave={async (annotations: State["annotations"]) => {
        try {
          setState({ ...state, saving: true });
          setTimeout(async () => {
            const jobOutput = job.contents.filter(c => c.role === OUTPUT_ROLE);
            if (jobOutput.length < 1) {
              throw new Error("Could not find output content.");
            }
            const xml = insertDataPoints(annotations, xmlDocument);
            console.log(xml);
            await opts.actions.saveContent(jobOutput[0].id, xml);
            await opts.actions.checkinJobSuccess();
          }, 0);
        } catch (err) {
          setState({ ...DEFAULT_STATE, error: err });
        }
      }}
      predictions={predictions}
      saving={saving}
      taxonomies={taxonomies}
    />
  );
};
Handler.displayName = "Handler";
