import React from "react";
import { Col, Row } from "reactstrap";

import { IIndexingDocument, LinkType } from "../types";
import { DocumentSources } from "./DocumentSources";
import { Metadata } from "./Metadata";
import { TextBody } from "./TextBody";
import { Title } from "./Title";

interface IProps {
  readonly document: IIndexingDocument;
}

export const DocumentContents = ({ document }: IProps) => (
  <div className="document-contents">
    <Row>
      <Col>
        <Title title={document.title} />
      </Col>
    </Row>
    <Row>
      <Col>
        <DocumentSources
          html={document.links[LinkType.HTML]}
          pdf={document.links[LinkType.PDF]}
          xml={document.links[LinkType.XML]}
        />
      </Col>
    </Row>
    <Row>
      <Col>
        <TextBody body={document.body} />
      </Col>
    </Row>
    <Row>
      <Col>
        <Metadata metadata={document.metadata} />
      </Col>
    </Row>
  </div>
);
DocumentContents.displayName = "DocumentContents";
