export enum UpdateMessageType {
  ADD_ANNOTATIONS,
  DELETE_ANNOTATIONS,
  REDO,
  REORDED_ANNOTATIONS,
  SORTED_ANNOTATIONS,
  UNDO,
}

interface IBaseMessage {
  taxonomyId: string;
  type: UpdateMessageType;
}

interface IAddedAnnotationsMessage extends IBaseMessage {
  terms: ReadonlyArray<string>;
  type: UpdateMessageType.ADD_ANNOTATIONS;
}
interface IDeletedAnnotationsMessage extends IBaseMessage {
  terms: ReadonlyArray<string>;
  type: UpdateMessageType.DELETE_ANNOTATIONS;
}
interface IRedoMessage extends IBaseMessage {
  type: UpdateMessageType.REDO;
}
interface IReorderedAnnotationsMessage extends IBaseMessage {
  direction: "UP" | "DOWN";
  terms: ReadonlyArray<string>;
  type: UpdateMessageType.REORDED_ANNOTATIONS;
}
interface ISortedAnnotationsMessage extends IBaseMessage {
  by: "ALPHA" | "CONF";
  direction: "ASC" | "DESC";
  type: UpdateMessageType.SORTED_ANNOTATIONS;
}
interface IUndoMessage extends IBaseMessage {
  type: UpdateMessageType.UNDO;
}

export type UpdateMessage =
  | IAddedAnnotationsMessage
  | IDeletedAnnotationsMessage
  | IRedoMessage
  | IReorderedAnnotationsMessage
  | ISortedAnnotationsMessage
  | IUndoMessage;

export type PostMessage = (m: UpdateMessage | null) => void;
