import { mode as getMode, stdDev as getStdDev } from "@core/lib/stats";
import { ATTR_FONT_SIZE, TAG_FONT } from "./constants";
import { IFontInformation } from "./types";

export const extractSizeFromFontElement = (el: Element): number =>
  parseFloat((el.getAttribute(ATTR_FONT_SIZE) || "").trim().replace(/pt$/, ""));

export const extractFontInformation = (root: Element): IFontInformation | undefined => {
  const fontTags = root.getElementsByTagName(TAG_FONT);
  const fontSizes: Array<number> = [];
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < fontTags.length; i++) {
    const fontSize = extractSizeFromFontElement(fontTags[i]);
    if (!isNaN(fontSize)) {
      fontSizes.push(fontSize);
    }
  }
  const mode = getMode(fontSizes);
  if (mode === undefined) {
    return undefined;
  }
  const stdDev = getStdDev(fontSizes);
  if (stdDev === undefined) {
    return { lowerBound: mode, mode, upperBound: mode };
  }
  return { lowerBound: mode - 2 * stdDev, mode, upperBound: mode + 2 * stdDev };
};
