import React from "react";

import { IActionMap } from "../../state/actions";
import { IAnnotation, Taxonomy } from "../../types";
import { List } from "./List";
import { Search } from "./Search";

interface IProps {
  readonly actions: IActionMap;
  readonly annotations: ReadonlyArray<IAnnotation>;
  readonly inputOrder: number;
  readonly maximumTermLength: number;
  readonly taxonomy: Taxonomy;
}

export const AnnotationsList = (props: IProps) => (
  <>
    <Search
      canAddNewTerms={props.taxonomy.open ? true : props.taxonomy.allowNewTerms}
      onAddAnnotation={(term: string) => {
        props.actions.addAnnotation(term, props.taxonomy.id);
      }}
      {...props}
    />
    <List {...props} />
  </>
);
AnnotationsList.displayName = "AnnotationsList";
