import React from "react";

import { AnnotationCheckbox } from "./AnnotationCheckbox";

interface ILeftActionsProps {
  readonly checked: boolean;
  readonly onToggleCheck: () => void;
  readonly taxonomyId: string;
  readonly term: string;
}
export const LeftActions = ({ checked, onToggleCheck, taxonomyId, term }: ILeftActionsProps) => (
  <div className="left">
    <div className="contents">
      <AnnotationCheckbox
        checked={checked}
        id={term}
        onChange={onToggleCheck}
        taxonomyId={taxonomyId}
      />
    </div>
  </div>
);
LeftActions.displayName = "LeftActions";
