import React from "react";

import { IActionMap } from "../../../state/actions";
import { IAnnotation, Taxonomy } from "../../../types";
import { ActionsRow } from "./ActionsRow";

interface IProps {
  readonly actions: IActionMap;
  readonly taxonomy: Taxonomy;
}

const EMPTY_ANNOTATIONS: ReadonlyArray<IAnnotation> = [];
const EMPTY_CHECKED: ReadonlyArray<string> = [];
export const EmptyList = ({ actions, taxonomy }: IProps) => (
  <div className="annotations-list">
    <ActionsRow
      actions={actions}
      annotations={EMPTY_ANNOTATIONS}
      checkedTerms={EMPTY_CHECKED}
      taxonomyId={taxonomy.id}
    />
    <div className="annotations-list-row empty">
      <p className="text-muted">No terms in {taxonomy.name}.</p>
    </div>
  </div>
);
EmptyList.displayName = "EmptyList";
