import { IJobType } from "@core/api-types/entities/jobType";
import { apiEndpointV1, request, Routes } from "./index";

export const getJobTypes = async (): Promise<IJobType[]> => {
  const res = await request<Routes.JobTypes, "GET">({ url: `${apiEndpointV1}/job-types` });

  return res.body!.response.list;
};
