import React from "react";

import { IJob } from "@core/api-types/entities/job";
import { InnodomContent } from "@ui/components/InnodomContent";
import { extractFontInformation } from "@ui/components/InnodomContent/util";
import { State } from "../Indexing/state";
import { IIndexingDocument, IPredictions } from "../Indexing/types";

const TAG_CONTENT = "inno:content";
const TAG_DATAPOINT = "inno:datapoint";
const TAG_META = "inno:meta";
const ATTR_CONFIDENCE = "confidence";
const ATTR_KEY = "key";

const CONTENT_INPUT = "input";
const CONTENT_HTML = "indexing-link-html";
const CONTENT_PDF = "indexing-link-pdf";
const CONTENT_XML = "indexing-link-xml";

const buildDocument = (
  job: IJob,
  token: string,
  content: Element,
  imageUris: Array<string>,
): IIndexingDocument => {
  const fontInformation = extractFontInformation(content);
  const inputContent = job.contents.find(c => c.role === CONTENT_INPUT);
  let title = `Job ${job.id}`;
  if (inputContent && inputContent.name && inputContent.name.length > 0) {
    title = inputContent.name;
  }
  const body = [];
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < content.childNodes.length; i++) {
    const block = content.childNodes[i];
    if (block instanceof Element) {
      body.push(
        <InnodomContent el={block} fontInformation={fontInformation} imageUris={imageUris} />,
      );
    }
  }
  const links = {
    HTML: "",
    PDF: "",
    XML: "",
  };
  const htmlLink = job.contents.find(c => c.role === CONTENT_HTML);
  if (htmlLink) {
    links.HTML = `${htmlLink.uri}?token=${encodeURIComponent(token)}`;
  }
  const pdfLink = job.contents.find(c => c.role === CONTENT_PDF);
  if (pdfLink) {
    links.PDF = `${pdfLink.uri}?token=${encodeURIComponent(token)}`;
  }
  const xmlLink = job.contents.find(c => c.role === CONTENT_XML);
  if (xmlLink) {
    links.XML = `${xmlLink.uri}?token=${encodeURIComponent(token)}`;
  }
  const metadata = {};
  const metadataKeys = Object.keys(job.metadata).filter(k => k.length > 1 && k.charAt(0) === ".");
  for (const metadataKey of metadataKeys) {
    metadata[metadataKey.substr(1)] = job.metadata[metadataKey];
  }
  return {
    body,
    links,
    metadata,
    title,
  };
};

const buildPredictions = (predictions: HTMLCollectionOf<Element>): IPredictions => {
  const toReturn: IPredictions = {};
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < predictions.length; i++) {
    const fullKey = (predictions[i].getAttribute(ATTR_KEY) || "").toLowerCase();
    const keySplit = fullKey.split(".").map(s => s.trim());
    if (keySplit.length !== 2) {
      continue;
    }
    const predictionKey = keySplit[0];
    const predictionIndex = parseInt(keySplit[1], 10);
    if (isNaN(predictionIndex)) {
      continue;
    }
    const confidence = parseFloat((predictions[i].getAttribute(ATTR_CONFIDENCE) || "").trim());
    const prediction = (predictions[i].textContent || "").trim();
    if (predictionKey.length < 1 || prediction.length < 1) {
      continue;
    }
    const newPrediction = {
      confidence: isNaN(confidence) ? 0.0 : confidence,
      prediction,
    };
    if (!(predictionKey in toReturn)) {
      toReturn[predictionKey] = [];
    }
    toReturn[predictionKey][predictionIndex] = newPrediction;
  }
  // and now flatten the arrays by removing any interspersed undefined value
  Object.keys(toReturn).forEach(predictionKey => {
    toReturn[predictionKey] = toReturn[predictionKey].filter(p => p !== undefined);
  });
  return toReturn;
};

interface IInnodomParseResponse {
  document: IIndexingDocument | null;
  error: Error | null;
  predictions: IPredictions | null;
  xmlDocument: Document | null;
}
export const buildDocumentAndPredictions = (
  job: IJob,
  token: string,
  xml: string,
  imageUris: Array<string>,
): IInnodomParseResponse => {
  const parser = new DOMParser();
  const xmlDoc = parser.parseFromString(xml, "text/xml");
  const errors = xmlDoc.getElementsByTagName("parsererror");
  if (errors.length > 0) {
    return {
      document: null,
      error: new Error(`Error parsing XML: ${new XMLSerializer().serializeToString(xmlDoc)}`),
      predictions: null,
      xmlDocument: null,
    };
  }
  const { documentElement } = xmlDoc;
  if (documentElement && documentElement instanceof Element) {
    const content = documentElement.getElementsByTagName(TAG_CONTENT);
    const metadata = documentElement.getElementsByTagName(TAG_META);
    if (content.length > 0 && metadata.length > 0) {
      try {
        return {
          document: buildDocument(job, token, content[0], imageUris),
          error: null,
          predictions: buildPredictions(metadata[0]!.getElementsByTagName(TAG_DATAPOINT)),
          xmlDocument: xmlDoc,
        };
      } catch (err) {
        return { document: null, error: err, predictions: null, xmlDocument: null };
      }
    }
  }
  return {
    document: null,
    error: new Error("Invalid or empty xml."),
    predictions: null,
    xmlDocument: null,
  };
};

const insertDataPoint = (
  root: Document,
  meta: Element,
  key: string,
  confidence: number,
  value: string,
) => {
  const newDataPointTag = root.createElement(TAG_DATAPOINT);
  newDataPointTag.setAttribute(ATTR_KEY, key);
  newDataPointTag.setAttribute(ATTR_CONFIDENCE, confidence.toString(10));
  newDataPointTag.appendChild(root.createTextNode(value));
  meta.appendChild(newDataPointTag);
};

export const insertDataPoints = (annotations: State["annotations"], root: Document): string => {
  // remove old meta tags
  const metaTags = root.getElementsByTagName(TAG_META);
  // tslint:disable-next-line: prefer-for-of
  for (let i = 0; i < metaTags.length; i++) {
    metaTags[i].remove();
  }
  const newMetaTag = root.createElement(TAG_META);
  Object.keys(annotations).forEach(taxonomyKey => {
    for (let i = 0; i < annotations[taxonomyKey].length; i++) {
      const key = `${taxonomyKey}.${i + 1}`;
      let confidence = annotations[taxonomyKey][i].confidence;
      if (isNaN(confidence)) {
        confidence = 0.0;
      }
      if (annotations[taxonomyKey][i].humanAdded) {
        confidence = 1.0;
      }
      insertDataPoint(root, newMetaTag, key, confidence, annotations[taxonomyKey][i].term);
    }
  });
  root.documentElement.appendChild(newMetaTag);
  const serializer = new XMLSerializer();
  return serializer.serializeToString(root);
};
