import { ReactNode } from "react";

export interface IPrediction {
  confidence: number;
  prediction: string;
}

export interface IPredictions {
  [taxonomyKey: string]: Array<IPrediction>;
}

export enum LinkType {
  HTML = "HTML",
  PDF = "PDF",
  XML = "XML",
}

export interface IIndexingDocument {
  body: Array<ReactNode>;
  links: Record<LinkType, string>;
  metadata?: {
    [key: string]: any;
  };
  title: string;
}

export interface ITaxonomyTerm {
  term: string;
  help?: string;
  children?: Array<string>;
  preferred?: Array<string>;
}
interface IBaseTaxonomy {
  highConfidenceThreshold?: number;
  id: string;
  minimumPredictions?: number;
  maximumPredictions?: number;
  name: string;
  open: boolean;
}
interface IOpenTaxonomy extends IBaseTaxonomy {
  open: true;
}
interface IClosedTaxonomy extends IBaseTaxonomy {
  allowNewTerms: boolean;
  open: false;
  terms: {
    [term: string]: ITaxonomyTerm;
  };
}
export type Taxonomy = IClosedTaxonomy | IOpenTaxonomy;
export interface ITaxonomies {
  [key: string]: Taxonomy;
}

export interface IInputTaxonomy {
  id: string;
  maximumPredictions?: number;
  minimumPredictions?: number;
  name: string;
}

export const isTaxonomyClosed = (input: Taxonomy): input is IClosedTaxonomy => !input.open;
export const isTaxonomyOpen = (input: Taxonomy): input is IOpenTaxonomy => input.open;

export interface IAnnotation {
  confidence: number;
  humanAdded: boolean;
  term: string;
  termLowerCase: string;
}

export interface IAnnotations {
  [taxonomyKey: string]: Array<IAnnotation>;
}
