import { ReactElement, useEffect, useReducer, useState } from "react";

import { getPersistentState, setPersistentState } from "@ui/util/persistState";
import { IPredictions, ITaxonomies } from "../types";
import { actionMap, ActionType, IActionMap } from "./actions";
import { reducer, State } from "./index";
import { UpdateMessage } from "./messages";
import { useDefaultState } from "./useDefaultState";

export type ClearMessage = () => void;

interface IChildrenArgs {
  readonly actions: IActionMap;
  readonly clearMessage: ClearMessage;
  readonly message: UpdateMessage | null;
  readonly state: State;
}
interface IProps {
  readonly children: (args: IChildrenArgs) => ReactElement;
  readonly initialPredictions: IPredictions;
  readonly jobId: string;
  readonly taxonomies: ITaxonomies;
}

const LOAD_PERSISTED_STATE_MESSAGE = "Would you like to load your previous work for this job?";
const STATE_APP_ID = "indexing-workbench";
const STATE_VERSION = "1";

export const StateManager = ({ children, initialPredictions, jobId, taxonomies }: IProps) => {
  const [message, setMessage] = useState<UpdateMessage | null>(null);
  const defaultState = useDefaultState(initialPredictions, taxonomies);
  const [isFirstRun, setFirstRun] = useState(true);
  const [state, dispatch] = useReducer(reducer, defaultState);
  const clearMessage = () => {
    setMessage(null);
  };
  useEffect(() => {
    if (!isFirstRun) {
      return;
    }
    setFirstRun(false);
    const [, persistedState] = getPersistentState(STATE_APP_ID, jobId);
    if (persistedState !== null) {
      if (JSON.stringify(persistedState) === JSON.stringify(state.annotations)) {
        return;
      }
      if (window.confirm(LOAD_PERSISTED_STATE_MESSAGE)) {
        dispatch({
          persistedState: persistedState as State["annotations"],
          postMessage: () => {
            // do nothing
          },
          type: ActionType.LOAD_PERSISTED_STATE,
        });
      }
    }
  });
  useEffect(() => {
    if (isFirstRun) {
      return;
    }
    setPersistentState(STATE_APP_ID, jobId, STATE_VERSION, state.annotations);
  }, [state.annotations]);
  return children({ actions: actionMap(dispatch, setMessage), clearMessage, message, state });
};
