import React, { useEffect, useMemo, useState } from "react";

import { IActionMap } from "../../../state/actions";
import { IAnnotation, isTaxonomyClosed, Taxonomy } from "../../../types";
import { ActionsRow } from "./ActionsRow";
import { AnnotationRow } from "./AnnotationRow";
import { EmptyList } from "./EmptyList";

interface IProps {
  readonly actions: IActionMap;
  readonly annotations: ReadonlyArray<IAnnotation>;
  readonly taxonomy: Taxonomy;
}

const DEFAULT_STATE = {};

export const List = ({ actions, annotations, taxonomy }: IProps) => {
  const [checked, setChecked] = useState(DEFAULT_STATE);
  const checkedTerms = useMemo(() => Object.keys(checked), [checked]);
  useEffect(() => {
    // find the checked annotations that are no longer in the list
    const removedTerms = checkedTerms.filter(
      t => !annotations.map(a => a.termLowerCase).includes(t),
    );
    if (removedTerms.length < 1) {
      return;
    }
    if (removedTerms.length === checkedTerms.length) {
      // we're removing all terms so set the checked state to the default
      setChecked(DEFAULT_STATE);
      return;
    }
    const newChecked = { ...checked };
    for (const term of removedTerms) {
      delete newChecked[term];
    }
    setChecked(newChecked);
  }, [annotations]);
  if (annotations.length < 1) {
    return <EmptyList actions={actions} taxonomy={taxonomy} />;
  }
  const onToggleCheck = (term: string) => () => {
    if (term in checked) {
      if (Object.keys(checked).length === 1) {
        setChecked(DEFAULT_STATE);
        return;
      }
      const newChecked = { ...checked };
      delete newChecked[term];
      setChecked(newChecked);
      return;
    }
    setChecked({ ...checked, [term]: true });
  };
  return (
    <div className="annotations-list">
      <ActionsRow
        actions={actions}
        annotations={annotations}
        checkedTerms={checkedTerms}
        taxonomyId={taxonomy.id}
      />
      {annotations.map(a => {
        let taxonomyTerm = null;
        if (isTaxonomyClosed(taxonomy) && a.term in taxonomy.terms) {
          taxonomyTerm = taxonomy.terms[a.term];
        }
        return (
          <AnnotationRow
            annotation={a}
            checked={a.termLowerCase in checked}
            expanded={false}
            key={a.termLowerCase}
            onToggleCheck={onToggleCheck(a.termLowerCase)}
            taxonomyId={taxonomy.id}
            taxonomyTerm={taxonomyTerm}
          />
        );
      })}
    </div>
  );
};
List.displayName = "List";
