import React from "react";

import { IIndexingDocument } from "../../types";
import { MetadataNode } from "./MetadataNode";

interface IProps {
  readonly metadata: IIndexingDocument["metadata"];
}
export const Metadata = ({ metadata }: IProps) => {
  if (!metadata || Object.keys(metadata).length < 1) {
    return null;
  }
  return (
    <div className="metadata">
      <MetadataNode metadata={metadata} />
    </div>
  );
};
