import React from "react";

import { Cell } from "./Cell";
import { Header } from "./Header";
import { Row as RowType } from "./util";

type Props = Readonly<{
  isHeader: boolean;
  row: RowType;
}>;

export const Row = ({ isHeader, row }: Props) => (
  <tr>
    {row.cells.map((cell, cellIndex) =>
      isHeader ? (
        <Header cell={cell} key={`cell-${cellIndex}`} />
      ) : (
        <Cell cell={cell} key={`cell-${cellIndex}`} />
      ),
    )}
  </tr>
);
Row.displayName = "Row";
