import { IAnnotation, IAnnotations } from "../types";
import { Action, ActionType } from "./actions";
import { UpdateMessageType } from "./messages";

export type State = IAnnotations;

type SortResponse = -1 | 0 | 1;
const sortByAlpha = (a: IAnnotation, b: IAnnotation): SortResponse => {
  if (a.termLowerCase < b.termLowerCase) {
    return -1;
  }
  if (a.termLowerCase !== b.termLowerCase) {
    return 1;
  }
  return 0;
};
const sortByConfidence = (a: IAnnotation, b: IAnnotation): SortResponse => {
  if (a.humanAdded) {
    if (b.humanAdded) {
      return sortByAlpha(a, b);
    }
    return -1;
  }
  if (b.humanAdded) {
    return 1;
  }
  if (a.confidence < b.confidence) {
    return -1;
  }
  if (a.confidence !== b.confidence) {
    return 1;
  }
  return sortByAlpha(a, b);
};

// tslint:disable-next-line: cyclomatic-complexity
export const annotations = (state: State, action: Action): State => {
  switch (action.type) {
    case ActionType.ADD_ANNOTATION: {
      const cleanTerm = action.term.trim().toLowerCase();
      // ensure the list of annotations doesn't already include this term
      if (
        cleanTerm.length < 1 ||
        !(action.taxonomyId in state) ||
        state[action.taxonomyId].map(a => a.termLowerCase).includes(cleanTerm)
      ) {
        return state;
      }
      const newTerm: IAnnotation = {
        confidence: 0,
        humanAdded: true,
        term: action.term,
        termLowerCase: cleanTerm,
      };
      action.postMessage({
        taxonomyId: action.taxonomyId,
        terms: [action.term],
        type: UpdateMessageType.ADD_ANNOTATIONS,
      });
      return {
        ...state,
        [action.taxonomyId]: [newTerm, ...state[action.taxonomyId]],
      };
    }
    case ActionType.DELETE_ANNOTATIONS: {
      if (!(action.taxonomyId in state) || action.annotations.length < 1) {
        return state;
      }
      const termsToKeep = state[action.taxonomyId].filter(
        a => !action.annotations.includes(a.termLowerCase),
      );
      if (termsToKeep.length === state[action.taxonomyId].length) {
        return state;
      }
      action.postMessage({
        taxonomyId: action.taxonomyId,
        terms: action.annotations,
        type: UpdateMessageType.DELETE_ANNOTATIONS,
      });
      return {
        ...state,
        [action.taxonomyId]: termsToKeep,
      };
    }
    case ActionType.REORDER_ANNOTATIONS: {
      if (
        !(action.taxonomyId in state) ||
        action.annotations.length < 1 ||
        state[action.taxonomyId].length < 1
      ) {
        return state;
      }
      let reordered = state[action.taxonomyId];
      let swapped = false;
      switch (action.direction) {
        case "UP": {
          reordered = [...reordered];
          for (let i = 1; i < reordered.length; i++) {
            if (
              !action.annotations.includes(reordered[i].termLowerCase) ||
              action.annotations.includes(reordered[i - 1].termLowerCase)
            ) {
              continue;
            }
            swapped = true;
            const swap = reordered[i - 1];
            reordered[i - 1] = reordered[i];
            reordered[i] = swap;
          }
          break;
        }
        case "DOWN": {
          reordered = [...reordered];
          for (let i = reordered.length - 2; i >= 0; i--) {
            if (
              !action.annotations.includes(reordered[i].termLowerCase) ||
              action.annotations.includes(reordered[i + 1].termLowerCase)
            ) {
              continue;
            }
            swapped = true;
            const swap = reordered[i + 1];
            reordered[i + 1] = reordered[i];
            reordered[i] = swap;
          }
          break;
        }
      }
      if (!swapped) {
        return state;
      }
      action.postMessage({
        direction: action.direction,
        taxonomyId: action.taxonomyId,
        terms: action.annotations,
        type: UpdateMessageType.REORDED_ANNOTATIONS,
      });
      return {
        ...state,
        [action.taxonomyId]: reordered,
      };
    }
    case ActionType.SORT_ANNOTATIONS: {
      if (!(action.taxonomyId in state) || state[action.taxonomyId].length < 1) {
        return state;
      }
      let sorted = state[action.taxonomyId];
      switch (action.by) {
        case "ALPHA":
          sorted = sorted.sort(sortByAlpha);
          break;
        case "CONF":
          sorted = sorted.sort(sortByConfidence);
          break;
      }
      action.postMessage({
        by: action.by,
        direction: action.direction,
        taxonomyId: action.taxonomyId,
        type: UpdateMessageType.SORTED_ANNOTATIONS,
      });
      return {
        ...state,
        [action.taxonomyId]: action.direction === "DESC" ? sorted : sorted.reverse(),
      };
    }
    default:
      return state;
  }
};
