import React, { useEffect, useState } from "react";

import { TAG_BLOCK } from "../constants";

type Props = Readonly<{
  el: Element;
  imageUris: Array<string>;
}>;

export const InnodomImage = ({ el, imageUris }: Props) => {
  if (imageUris.length < 1) {
    return null;
  }
  const blocks = el.getElementsByTagName(TAG_BLOCK);
  if (blocks.length < 1) {
    return null;
  }
  const block = blocks[0];
  const [page, pageHeight, xMin, xMax, yMin, yMax] = [
    "page",
    "page-height",
    "x-min",
    "x-max",
    "y-min",
    "y-max",
  ].map(attr => parseFloat(block.getAttribute(attr) || ""));
  const width = xMax - xMin;
  const height = yMax - yMin;
  if (
    isNaN(page) ||
    page < 1 ||
    page > imageUris.length ||
    isNaN(width) ||
    isNaN(height) ||
    isNaN(pageHeight)
  ) {
    console.error("Unable to render image:", block.outerHTML);
    return null;
  }
  const [dataUri, setDataUri] = useState<string | null>(null);
  useEffect(() => {
    const image = new Image();
    image.crossOrigin = "anonymous";
    image.src = imageUris[page - 1];
    image.onload = () => {
      const scale = image.naturalHeight / pageHeight;
      if (isNaN(scale)) {
        return;
      }
      const canvas = document.createElement("canvas");
      canvas.width = width * scale;
      canvas.height = height * scale;
      const context = canvas.getContext("2d");
      if (!context) {
        return;
      }
      context.drawImage(
        image,
        xMin * scale,
        yMin * scale,
        width * scale,
        height * scale,
        0,
        0,
        width * scale,
        height * scale,
      );
      setDataUri(canvas.toDataURL());
    };
  }, []);
  if (dataUri === null) {
    return null;
  }
  const styles = {
    height: "auto",
    maxWidth: "100%",
    width: "auto",
  };
  return <img className="d-block" src={dataUri} style={styles} />;
};
InnodomImage.displayName = "InnodomImage";
