import React from "react";

import { IAnnotation, ITaxonomyTerm } from "../../../../types";
import { ExpandedDetails } from "./ExpandedDetails";
import { InnerContents } from "./InnerContents";
import { LeftActions } from "./LeftActions";
import { RightActions } from "./RightActions";

interface IProps {
  readonly annotation: IAnnotation;
  readonly checked: boolean;
  readonly expanded: boolean;
  readonly onToggleCheck: () => void;
  readonly taxonomyId: string;
  readonly taxonomyTerm: ITaxonomyTerm | null;
}
export const AnnotationRow = ({
  annotation,
  checked,
  expanded,
  onToggleCheck,
  taxonomyId,
  taxonomyTerm,
}: IProps) => (
  <>
    <div className="annotations-list-row" onClick={onToggleCheck}>
      <LeftActions
        checked={checked}
        onToggleCheck={onToggleCheck}
        taxonomyId={taxonomyId}
        term={annotation.term}
      />
      <InnerContents annotation={annotation} taxonomyId={taxonomyId} taxonomyTerm={taxonomyTerm} />
      {taxonomyTerm && (
        <RightActions taxonomyId={taxonomyId} taxonomyTerm={taxonomyTerm} term={annotation.term} />
      )}
    </div>
    <ExpandedDetails annotation={annotation} expanded={expanded} taxonomyId={taxonomyId} />
  </>
);
