import cx from "classnames";
import React, { useMemo, useState } from "react";
import { Input, InputGroup, InputGroupAddon } from "reactstrap";

import { Taxonomy } from "../../../types";

interface IProps {
  readonly canAddNewTerms: boolean;
  readonly inputOrder: number;
  readonly maximumTermLength: number;
  readonly onAddAnnotation: (term: string) => void;
  readonly taxonomy: Taxonomy;
}

export const Search = ({
  canAddNewTerms,
  inputOrder,
  maximumTermLength,
  onAddAnnotation,
  taxonomy,
}: IProps) => {
  const [focused, setFocused] = useState(false);
  const [text, setText] = useState("");
  const placeholder = useMemo(() => {
    if (taxonomy.open) {
      return `Add new term`;
    }
    if (canAddNewTerms) {
      return `Search in ${taxonomy.name} or add new term`;
    }
    return `Search in ${taxonomy.name}`;
  }, [canAddNewTerms, taxonomy]);
  return (
    <div
      className="annotations-search"
      onMouseDown={e => {
        e.stopPropagation();
      }}
    >
      <InputGroup>
        <InputGroupAddon addonType="prepend">+</InputGroupAddon>
        <Input
          className={cx("annotations-search-input", { open: focused })}
          disabled={taxonomy.open && !canAddNewTerms}
          label={taxonomy.name}
          maxLength={maximumTermLength}
          onBlur={() => {
            setFocused(false);
          }}
          onChange={e => {
            setText(e.target.value);
          }}
          onFocus={() => {
            setFocused(true);
          }}
          onKeyDown={e => {
            // listen for "Enter" key
            if (e.keyCode === 13) {
              e.preventDefault();
              e.stopPropagation();
              const trimmedText = text.trim();
              if (trimmedText.length > 0) {
                onAddAnnotation(trimmedText);
                setText("");
              }
            }
          }}
          placeholder={placeholder}
          tabIndex={inputOrder}
          type={taxonomy.open ? "text" : "search"}
          value={text}
        />
      </InputGroup>
    </div>
  );
};
Search.displayName = "Search";
