import React, { useMemo } from "react";

import { TAG_IMAGE, TAG_MATH, TAG_TABLE, TAG_TEXT } from "./constants";
import { InnodomImage as Image } from "./InnodomImage";
import { Table } from "./Table";
import { Text } from "./Text";
import { IFontInformation } from "./types";

type Props = Readonly<{
  el: Element;
  fontInformation?: IFontInformation;
  imageUris: Array<string>;
}>;

export const InnodomContent = (props: Props) =>
  useMemo(() => {
    const { el } = props;
    switch (el.nodeName) {
      case TAG_TABLE:
        return <Table {...props} />;
      case TAG_TEXT:
        return <Text {...props} />;
      case TAG_MATH:
      case TAG_IMAGE:
        return <Image {...props} />;
      default:
        console.warn("Unknown block:", el.nodeName);
        return null;
    }
  }, [props.el.outerHTML]);
